.App {
  text-align: center;
}

.App-textarea {
  width: 50%;
  padding: 20px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.App-button {
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 18px;
  margin-top: 10px;;
}

